*{
  margin: 0;
  padding: 0;
}

#journal-scroll::-webkit-scrollbar {
  width: 4px;
  cursor: pointer;
  /*background-color: rgba(229, 231, 235, var(--bg-opacity));*/

}
#journal-scroll::-webkit-scrollbar-track {
  background-color: rgba(229, 231, 235, var(--bg-opacity));
  cursor: pointer;
  /*background: red;*/
}
#journal-scroll::-webkit-scrollbar-thumb {
  cursor: pointer;
  background-color: #a0aec0;
  /*outline: 1px solid slategrey;*/
}

.schdule{
  width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  top: 0;
  right: 0;
  animation: in 1s forwards;
}

.fill{
  position: absolute;
  top: 0;
  right: 0;
  width: 75%;
  height: 100vh;
}

.App {
  text-align: center;
  background-color: #1a1a1a;
	height: 100vh;
}

.fredoka{
  font-family: 'Fredoka One', cursive;
}

.patua{
  font-family: 'Patua One', cursive;
}

.link{
  transition: .5s linear;
}

.bodyCard{
  max-height: 80vh;
}

.navLink{
  letter-spacing: 2px;
  transition: .5s linear;
  font-size: 1.3em;
}

.link:hover .navLink{
  letter-spacing: 1px;
  transform: translateX(10px);
}

.in{
  background: none;
  border: none;
  outline: none;
  height: 50px;
  position: absolute;
  font-size: 20px;
  letter-spacing: 2px;
  width: 140px;
  margin-right: 20px;
}

.iconNav{
  animation: p 3s linear infinite alternate;
}

.src{
  display: none;
  background: none;
  height: 40px;
  outline: none;
  border-bottom: 1px solid white;
}

.navi{
  display: none;
}

.logo{
  width: 130px;
}

.tHome{
  font-size: 20px;
}

.cTitle{
  font-size: 40px;
  animation: typing 4s steps(15) infinite alternate, blink .3s step-end infinite alternate;
  white-space: nowrap;
  border-right: 1px solid white;
  overflow: hidden;
  width: 0;
  text-align: left;
}

@keyframes typing{
  70%, 100%{
    width: 11ch;
  }
}

@keyframes blink{
  50%{
    border: transparent;
  }
}

@keyframes in {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes p {
  0%{
    transform: translate(2px,-4px);
  }
  100%{
    transform: translate(-2px,4px) rotate(10deg);
  }
}

@media (min-width: 1024px){
  .navi{
    display: block;
  }
}

@media (max-width: 1024px){
  .in{
    width: 110px;
  }
  .searchIcon{
    translate: -20px 0;
  }
  .logo{
    width: 100px;
  }
  .navi{
    display: block;
  }
}

@media (max-width: 834px){

  .logo{
    width: 60px;
  }

  .navi{
    display: none;
  }

  .navList{
    margin: 0;
    display: flex;
    
  }

  .link{
    width: 10%;
  }

  .content{
    width: 100%;
    /* background-color: blue; */
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .fill{
    width: 100%;
  }

  .src{
    display: block;
  }

  .App{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .btnHome{
    position: absolute;
    left: 50%;
  }

  .schdule{
    width: 100%;
  }

  .listBtn{
    margin-left: 80px;
  }

  .bodyCard{
    max-height: 89vh;
  }

}

@media (max-width: 566px){
  .cTitle{
    font-size: 20px;
  }

  .schdule{
    width: 100%;
  }

  .tHome{
    font-size: 15px;
  }

  .btnHome{
    position: absolute;
    left: 43%;
  }

  .listBtn{
    height: 85vh;
    overflow-y: scroll;
    margin-left: 0px;
  }

  .bodyCard{
    max-height: 86vh;
  }

  .src{
    width: 100px;
  }
}